* {
  margin: 0;
  padding: 0;
}

:root {
  --bg-secundary: #ffc43a;
  --bg-primary: #fafafa;
  --font: 'Poppins', sans-serif;
  --fontColor-Primary: #282828;
  --fontColor-Secondary: #ffc43a;
}

body {
  background: #fafafa;
  background: var(--bg-primary);
}

.container {
    position: relative;
    height: 100%;
}
   
.inner {
    position: absolute;
    width: 100%;
}
   
.fade-enter,
.fade-appear {
    opacity: 0;
}
   
.fade-enter.fade-enter-active,
.fade-appear.fade-appear-active {
    opacity: 1;
    transition: opacity 400ms;
}
   
.fade-exit {
    opacity: 1;
}
   
.fade-exit.fade-exit-active {
    opacity: 0;
    transition: opacity 200ms;
}
header {
  display: flex;
  justify-content: space-around;
  margin-top: 30px;
  /*width: 100%;
  height: 90px;
  background-color: #ffc33c;
  border-radius: 0 0 1.5em 1.5em;*/
}

header .header-search {
  /*position: relative;*/
  display: flex;
  justify-content: center;
  /*top: 37px;*/
}

main {
  margin-top: 25px;
  margin-bottom: 60px;
}

main .main-random {
  margin-left: 20px;
}

main .main-similar {
  margin-left: 20px;
}

/*.icon {
    padding: 0.6em 1em;
}*/
.Search-input,
.Search-btn {
  border: none;
  outline: none;
  height: 1em;
}

.Search-btn {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 2.6em;
  background-color: var(--bg-secundary);
  border-radius:  10px 0 0 10px;
}

.Search-input {
  width: 145px;
  height: 1.1em;
  padding: 0.6em 2.6em 0.6em 1em;
  background-color: #FFF;
  font-family: var(--font);
  color: var(--fontColor-Primary);
  font-size: 1em;
  font-weight: 400;
  border: none;
  outline: none;
  border-radius: 0 10px 10px 0;
  background-repeat: no-repeat;
  background-position-y: center;
  background-position-x: 95%;
}

.filtersBtn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 2.6em;
    border-radius: 10px;
    background-color: var(--bg-secundary);
    border: none;
    outline: none;
}
.wrapper {
    position: absolute;
    display: flex;
    justify-content: center;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.4);
}

.window {
    margin-top: 50px;
    width: 320px;
    position: relative;
    background: #FFF;
    border-radius: 10px;
    padding: 15px;
    z-index: 10;
    overflow: hidden;
}

.window-header {
    position: relative;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.window .window-header .closeBtn {
    position: absolute;
    top: 0;
    right: 0;
    border-radius: 10px;
    background: inherit;
    border: none;
    outline: none;
    background-color: inherit;
}

.window .window-header h1 {
    font-family: var(--font); 
    color: var(--fontColor-Primary);
    font-size: 21px;
    font-weight: 500;
}

.window .window-header hr {
    width: 200%;
    border: none;
    border-bottom: solid 1px #dadce0;
}

.Food-Card {
    position: relative;
    margin-right: 20px;
}

.Food-Card .Food-Card--img {
    width: 300px;
    height: 200px;
    object-fit: cover;
    border-radius: 10px;
}

.Food-Card .Food-Card--title {
    position: absolute;
    top: 120px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    -webkit-backdrop-filter: blur(20px);
            backdrop-filter: blur(20px);
    background-color: rgba(255, 255, 255, 0.8);
    width: 200px;
    height: 60px;
    padding: 17px;
    padding-right: 20px;
    padding-left: 20px;
    border-radius: 15px;
    box-shadow: 0 5px 15px rgba(50, 50, 50, 0.1);
}

.Food-Card .Food-Card--title h2 {
    font-family: 'Poppins', sans-serif; 
    color: #282828;
    font-size: 18px;
    font-weight: 500;
}
.Random-Recipe-title {
    font-family: var(--font); 
    color: var(--fontColor-Primary);
    font-size: 21px;
}

.Random-Recipe--trending-foods {
    position: relative;
    display: flex;
    width: 330px;
    height: 230px;
    background-color: inherit;
    overflow: hidden;
    overflow: auto;
}

::-webkit-scrollbar {
    display: none;
}
.bottomnav {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100vw;
  box-shadow: -3px 0 10px rgb(0 0 0 / 20%);
  background-color: #FFF;
}

.bottomnav > ul {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.bottomnav > ul > li {
  list-style: none;
}

.similar-recipes {
    display: flex;
    justify-content: space-between;
}

.similar-recipes .similiar-recipes--h1 {
    font-family: var(--font);
    color: var(--fontColor-Primary);
    font-size: 21px;
}

.similar-recipes .similiar-recipes--link {
    display: flex;
    text-decoration: none;
}

.similar-recipes .similiar-recipes--link .similiar-recipes--link-p {
    font-family: var(--font);
    color: var(--fontColor-Secondary);
    font-size: 17px;
    margin-top: 18px;
    margin-right: -30px;
}

.main-similar-recipes--header .main-similiar-recipes--header-link .icon {
    margin-top: -2px;
}
.Recipe-Card--Link {
    text-decoration: none;
}

.Recipe-Card {
    display: flex;
    justify-content: center;
    margin-bottom: 10px;
}

.Recipe-Card .Recipe-Card--recipeImage .recipeImage {
    width: 90px;
    height: 90px;
    object-fit: cover;
    border-radius: 10px;
    margin-right: 10px;
}

.Recipe-Card .Recipe-Card--recipeContent h2 {
    font-family: var(--font);
    color: var(--fontColor-Primary);
    font-size: 1rem;
    margin-top: 7px;
    margin-bottom: 8px;
}

.Recipe-Card .Recipe-Card--recipeContent p {
    font-family: var(--font);
    color: #808080;
    font-size: 15px;
}


